import { Button, Col, Form, Input, Row, Select, message } from "antd";
import axios from "axios";
import React, { useState } from "react";

const { Option } = Select;

const PrintForm = () => {
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [regex, setRegex] = useState("");
  const [fontDots, setFontDots] = useState("");

  //
  const fontOptions = {
    "1(8dots)": "1",
    "2(12dots)": "2",
    "3(16dots)": "3",
    "4(24dots)": "4",
    "5(32dots)": "5",
    "6(14dots)": "6",
    "7(21dots)": "7",
    "8(14dots)": "8",
  };

  // fontDots 
  const fontDotsOptions = {
    "1": "8",
    "2": "12",
    "3": "16",
    "4": "24",
    "5": "32",
    "6": "14",
    "7": "21",
    "8": "14",
  };

  // 
  const handleFontChange = (value) => {
    console.log("Selected text type:",value)
    setFontDots(fontDotsOptions[value]);
  };

  // 
  const defaultRegex = /IMEI:(\d{15}),MAC:([A-Fa-f0-9]{12})/;

  const onFinish = async (values) => {
    setLoading(true);

    try {
      let { data, url, textString } = values;
      const pattern = regex || defaultRegex; // 

      // 
      const match = data.match(new RegExp(pattern));

      let groups = [];

      if (!match) {
        // 
        message.error("");
        return;
      }

      if (match) {
        // 
        for (let i = 1; i < match.length; i++) {
          groups.push(match[i] || "");
        }
      }

      console.log("Groups:", groups);

      // URL
      let finalUrl = url;
      for (let i = 0; i < groups.length; i++) {
        finalUrl = finalUrl.replace(`{group${i}}`, groups[i] || "");
      }
      console.log("Final URL:", finalUrl);

      // textStringgroups
      const textStringPattern = /\{group(\d+)\}/;
      const textStringMatch = textString.match(textStringPattern);
      if (textStringMatch) {
        const groupIndex = parseInt(textStringMatch[1]);
        if (groupIndex >= groups.length) {
          message.error(
            ""
          );
          return;
        }
        textString = groups[groupIndex];
      }
      console.log("Text to print:", textString);

      //  HTTP 
      await axios.post("http://localhost:8080/api/print", {
        ...values,
        data: data,
        dataMatches: groups,
        url: finalUrl,
        textString: textString, // 
        fontDots:fontDots //dots
      });
      // 
      message.success("");
      //  data 
      form.setFieldsValue({ data: "" });
    } catch (error) {
      // 
      message.error("");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div
      style={{ marginTop: "10px", marginLeft: "100px", marginRight: "100px" }}
    >
      <Form
        form={form}
        layout="vertical"
        onFinish={onFinish}
        initialValues={{
          venderId: "",
          productId: "",
          cellWidth: "1",
          errorCorrectionLevel: "0",
          rotation: "0",
          nbLabels: "1",
          qrcodeX: "",
          qrcodeY: "",
          dots: "",
          labelDistance: "",
          labelWidth: "",
          labelLength: "",
          url: "http://example.com/pid?imei={group0}&mac={group1}",
          data: "",
          textStringX: "",
          textStringY: "",
          textStringType: "",
          multipliCationX: 1,
          multipliCationY: 1,
          textString: "{group0}",
          fontDots:0
        }}
      >
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item
              label="ID"
              name="venderId"
              rules={[
                { required: true, message: "ID" },
                { pattern: /^[0-9a-fA-F]+$/, message: "ID" },
              ]}
            >
              <Input maxLength={4} />
            </Form.Item>

            <Form.Item
              label="ID"
              name="productId"
              rules={[
                { required: true, message: "ID" },
                { pattern: /^[0-9a-fA-F]+$/, message: "ID" },
              ]}
            >
              <Input maxLength={4} />
            </Form.Item>

            <Form.Item
              label=""
              name="cellWidth"
              rules={[
                { required: true, message: "" },
                { pattern: /^[1-9]|10$/, message: "" },
              ]}
            >
              <Select>
                {[...Array(10).keys()].map((i) => (
                  <Option key={i + 1} value={(i + 1).toString()}>
                    {i + 1}
                  </Option>
                ))}
              </Select>
            </Form.Item>

            <Form.Item
              label=""
              name="errorCorrectionLevel"
              rules={[
                { required: true, message: "" },
                { pattern: /^[0-3]$/, message: "" },
              ]}
            >
              <Select>
                <Option value="0"></Option>
                <Option value="1"></Option>
                <Option value="2"></Option>
                <Option value="3"></Option>
              </Select>
            </Form.Item>

            <Form.Item
              label=""
              name="rotation"
              rules={[
                { required: true, message: "" },
                { pattern: /^[0-4]$/, message: "" },
              ]}
            >
              <Select>
                <Option value="0">0 </Option>
                <Option value="1">90 </Option>
                <Option value="2">180 </Option>
                <Option value="3">270 </Option>
              </Select>
            </Form.Item>

            <Form.Item
              label=""
              name="nbLabels"
              rules={[
                { required: true, message: "" },
                { pattern: /^[1-9]\d*$/, message: "" },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="1mm "
              name="dots"
              rules={[{ required: true, message: " 1mm " }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="labelDistance"
              rules={[{ required: true, message: "" }]}
            >
              <Input />
            </Form.Item>

            {/*  */}
            <Form.Item
              label="X(mm)"
              name="textStringX"
              rules={[{ required: true, message: "" }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Y(mm)"
              name="textStringY"
              rules={[{ required: true, message: "" }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="multipliCationX"
              rules={[{ required: true, message: "" }]}
            >
              <Select>
                {[...Array(10).keys()].map((i) => (
                  <Option key={i + 1} value={(i + 1).toString()}>
                    {i + 1}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={12}>
            {/*  */}
            <Form.Item
              label=""
              name="multipliCationY"
              rules={[{ required: true, message: "" }]}
            >
              <Select>
                {[...Array(10).keys()].map((i) => (
                  <Option key={i + 1} value={(i + 1).toString()}>
                    {i + 1}
                  </Option>
                ))}
              </Select>
            </Form.Item>

            <Form.Item
              label=""
              name="textStringType"
              rules={[{ required: true, message: "" }]}
            >
              <Select onChange={handleFontChange}>
                {Object.keys(fontOptions).map((key) => (
                  <Option key={key} value={fontOptions[key]}>
                    {key}
                  </Option>
                ))}
              </Select>
            </Form.Item>
            {/*  */}

            <Form.Item
              label=""
              name="textString"
              rules={[{ required: false, message: "" }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="(mm)"
              name="labelWidth"
              rules={[
                { required: true, message: "" },
                { pattern: /^[0-9]+$/, message: "" },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="(mm)"
              name="labelLength"
              rules={[
                { required: true, message: "" },
                { pattern: /^[0-9]+$/, message: "" },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="(mm)"
              name="qrcodeX"
              rules={[{ required: true, message: "" }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="(mm)"
              name="qrcodeY"
              rules={[{ required: true, message: "" }]}
            >
              <Input />
            </Form.Item>

            {/*  */}
            <Form.Item label="" name="regex">
              <Input
                placeholder={defaultRegex}
                onChange={(e) => setRegex(e.target.value)}
              />
            </Form.Item>

            {/* data */}
            <Form.Item
              label=""
              name="data"
              rules={[{ required: true, message: "" }]}
            >
              <Input />
            </Form.Item>

            {/* URL  */}
            <Form.Item
              label="URL "
              name="url"
              rules={[{ required: true, message: " URL " }]}
            >
              <Input />
            </Form.Item>
          </Col>
        </Row>

        <Form.Item style={{ textAlign: "center" }}>
          <Button type="primary" htmlType="submit" loading={loading}>
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default PrintForm;
